import React from "react";
import styled from "styled-components";

const Main = styled.div`
    width: 100%;
    height: 15vw;
    background-color: #91d3ff;
    position: fixed;
    border-radius: 0 !important;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 2;
`;

function Title(props) {
    return (
        <Main>
            <div>
                &lt;&lt;
            </div>
            <div>
                제작중입니다.
            </div>
            <div>
                home
            </div>
        </Main>
    );
}

export default Title;