import React, { useRef, useState } from "react";
import styled, { css } from "styled-components";
import ReactDOM from 'react-dom';
import ControlledCarousel from "../ControlledCarousel";
import ProjectMarkdown from "./ProjectMarkdown";

const Outside = styled.div`
    font-size: 2vh !important;
    cursor: auto;
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #00000092;
`;

const Main = styled.div`
    width: 60vw;
    height: 80vh;
    background-color: ${props=>props.theme.darkTheme.bg4};
    border-radius: 0.5vw;
    display: flex;
    align-items: center;
    color: ${props=>props.theme.darkTheme.color};
    position: relative;

    & *{
        transition: none !important;
    }

    & > div{
        width: 50%;
    }
    
    & > div:first-child{
        display: flex;
        justify-content: center;
        flex-shrink: 0;
    }
`;

const Close = styled.img`
    position: fixed;
    cursor: pointer;
    border-radius: 0 !important;
    width: 3vw;
    height: 3vw;
    top: 10vh;
    right: 15vw;
`;

const Description = styled.div`
    height: 90%;
    padding: 2% 2.5% 2% 2%;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        width: 0;
        display: none;
    }
`;

const Title = styled.div`
    margin-bottom: 3%;
    color: ${({theme}) => theme.darkTheme.color5};

    & .title{
        font-size: 2em;
        font-weight: 600;
    }

    & .date{
        font-size: 1em;
        font-weight: 500;
        color: ${({theme}) => theme.darkTheme.color3};
    }
`;

const Summary = styled.div`
    margin-bottom: 4%;
    & span{
        font-weight: 600;
    }
    & p:has(.br){
        height: 1vh;
    }
`;

const Fringe = styled.div`
    display: flex;
    align-items: center;
    margin-bottom: 4%;

    & div:first-child{
        font-size: 1.3em;
        font-weight: 600;
        width: 25%;
        flex-shrink: 0;
        color: ${({theme}) => theme.darkTheme.color5};

        & img{
            width: 25%;
            margin-right: 10%;
        }
    }

    & a{
        cursor: pointer;
        color: ${({theme}) => theme.darkTheme.color4} !important;
        text-decoration: underline !important;
        display: block;
        line-height: 1.5em;
    }

    & a:hover{
        color: ${({theme}) => theme.darkTheme.color5} !important;
    }

    & li{
        width: 100%;
        margin-left: 35%;
    }

    & .skill{
        display: flex;
        width: 100%;
        height: 3vw;
        flex-wrap: wrap;
        
        & > img{
            border: 0.2vw double ${({theme}) => theme.darkTheme.bg2} !important;
            border-radius: 0.5vw !important;
            background-color: ${({theme}) => theme.darkTheme.bg10};
            padding: 1%;
            height: 100%;
            margin-right: 5%;
        }
    }
`;

const ReadmeButton = styled.button`
    padding: 1% 2%;
    color: white;
    background-color: gray;
    border-radius: 0.3vw;
    border: none;

    &:hover {
        opacity: 0.8;
    }
`;

const Line = styled.div`
    margin: 4% 0;
    border-width: 0.1px;
    border-bottom-style: solid;
`;

function Project(props){
    const [index, setIndex] = useState(0);
    const [readmeOn, setReadmeOn] = useState(false);
    const mainRef = useRef();

    const mainScrollTop = () => {
        const mainElement = mainRef.current;
        if (mainElement) {
            mainElement.scrollTop = 0;
        }
    }

    const link = (link)=>{
        if(link){
            window.open("about:blank").location.href = link;
        }
    }

    return ReactDOM.createPortal(
        <Outside>
            <Main ref={mainRef}>
                <div>
                    <ControlledCarousel data={props.data} index={index} setIndex={()=>setIndex()}/>
                </div>
                <Description>
                    <Title>
                        <div className="title">
                            {props.data.title}
                        </div>
                        <div className="date">
                            {props.data.date}
                        </div>
                    </Title>
                    <Summary>
                        {props.data.summary.map((text)=>(
                            <p dangerouslySetInnerHTML={{ __html: text.replace(/\n/g, '</br>') }} />
                        ))}
                    </Summary>
                    <ReadmeButton onClick={()=>{
                        setReadmeOn(true);
                        mainScrollTop();
                    }}>
                        &gt;&gt; ReadMe
                    </ReadmeButton>
                    <Line />
                    <Fringe>
                        <div>
                            <img src={`${process.env.PUBLIC_URL}/images/email.png`} />
                            URL
                        </div>
                        <ul>
                            {Object.entries(props.data.url).map(([key, val])=>(
                                <li>
                                    <a onClick={()=>link(val)} title={val}>{key}</a>
                                </li>
                            ))}
                        </ul>
                    </Fringe>
                    <Fringe>
                        <div>
                            <img src={`${process.env.PUBLIC_URL}/images/email.png`} />
                            Skill
                        </div>
                        <div className="skill">
                            {props.data.skill.map((skill)=>(
                                <img src={`${process.env.PUBLIC_URL}/images/project/${skill}.png`} title={skill}/>
                            ))}
                        </div>
                    </Fringe>
                </Description>
                {readmeOn&&(
                    <ProjectMarkdown
                        num={props.data.id}
                        setReadmeOn={()=>setReadmeOn(false)}
                    />
                )}
            </Main>
            <Close src={`${process.env.PUBLIC_URL}/images/close.png`} onClick={()=>{
                props.selectOff();
                setReadmeOn(false);
                setIndex(0);
            }} />
        </Outside>, document.body
    );
}

export default Project;