import React, { useEffect, useRef } from "react";
import Carousel from 'react-bootstrap/Carousel';
import styled, { css } from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css";

const Main = styled.div`
  width: 90%;
  height: 35vh;
  text-align: center;
  font-size: 1.2rem;
  line-height: 2.5em;

  & .carousel{
    position: static;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0.8vh double ${({theme}) => theme.darkTheme.color4} !important;
    border-radius: 0.5vh;
    padding: 2%;
    margin-top: 6vh;
  }
  & .carousel-indicators{
    margin: 0;
    height: 1%;
    width: 70%;
    top: 2%;
    left: 15%;
  }
  & a{
    height: 3rem;
    width: 10%;
    top: 21%;
  }
  & button{
    background-color: ${props=>props.theme.darkTheme.color} !important;
  }

  & .carousel-control-prev > span{
    background-image: url(${process.env.PUBLIC_URL}/images/lArrow${props=>props.theme.darkTheme.dark && 'W'}.png);
  }
  & .carousel-control-next > span{
    background-image: url(${process.env.PUBLIC_URL}/images/rArrow${props=>props.theme.darkTheme.dark && 'W'}.png);
  }

  & .carousel-control-next{
    right: 0;
  }

`;

const Img = styled.img`
  max-width: 100%;
  max-height: 25vh;
  display: block;
  margin: 0 auto;
`;

function ControlledCarousel(props){

  const handleSelect = (selectedIndex) => {
    props.setIndex(selectedIndex);
  };

  const handleNextClick = (event) => {
    if (props.index < props.data.image.length - 1){
      props.setIndex(props.index+1);
    } else{
      props.setIndex(0);
    }
  };

  const handlePrevClick = (event) => {
    if (props.index > 0){
      props.setIndex(props.index-1);
    } else{
      props.setIndex(props.data.image.length - 1);
    }
  };

  const carouselRef = useRef(null);
  useEffect(()=>{
    const carouselElement = carouselRef.current;
    if (carouselElement){
      const nextButton = document.querySelector('.carousel-control-next');
      if (nextButton) {
        nextButton.addEventListener('click', handleNextClick);
      }

      const prevButton = document.querySelector('.carousel-control-prev');
      if (prevButton) {
        prevButton.addEventListener('click', handlePrevClick);
      }

      return () => {
        if (nextButton) {
          nextButton.removeEventListener('click', handleNextClick);
        }
        if (prevButton) {
          prevButton.removeEventListener('click', handlePrevClick);
        }
      }
    }
  }, []);

  return (
    <Main>
      <Carousel ref={carouselRef} activeIndex={props.index} onSelect={handleSelect}>
        {props.data.image.map((img)=>(
          <Carousel.Item>
            <Img src={`${process.env.PUBLIC_URL}${img}`} />
          </Carousel.Item>
        ))}
      </Carousel>
    </Main>
  );
}

export default ControlledCarousel;