import React from "react";
import styled from "styled-components";

const Main = styled.div`
    width: 100%;
    height: 15vw;
    background-color: #91d3ff;
    position: fixed;
    border-radius: 0 !important;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    z-index: 2;

    & > div{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        &:hover{
            background-color: white;
        }
    }
`;

function Bottom(props) {

    const selectMenu = (num) =>{
        props.containerReset();
        props.setMenu(num);
    }

    return (
        <Main>
            <div onClick={()=>selectMenu(0)}>
                1
            </div>
            <div onClick={()=>selectMenu(1)}>
                2
            </div>
            <div onClick={()=>selectMenu(2)}>
                3
            </div>
            <div onClick={()=>selectMenu(3)}>
                4
            </div>
            <div>
                5
            </div>
        </Main>
    );
}

export default Bottom;