import './App.css';
import { useEffect, useState } from 'react';
import { ThemeProvider } from 'styled-components';
import Wrap from './components/Wrap';
import Wrap_Mobile from './components_mobile/Wrap';
import { light, dark } from './style/theme';
import GlobalStyle from './style/GlobalStyle';
import { IsDarkContext } from './context/IsDarkContext';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import PreLoadingImage from './components/PreLoadingImage';


function App() {
  const [isReady, setIsReady] = useState(false);

  const [ darkMode, setDarkMode ] = useState(false);
  const darkModeToggle = () => {
    if (darkMode){
      setDarkMode(false);
    } else {
      setDarkMode(true);
    }
  };

  const [verticalMode, setVerticalMode] = useState(false);
  useEffect(() => {
    const handleResize = () => {
      const viewportWidth = window.innerWidth;
      const viewportHeight = window.innerHeight;

      if (viewportWidth > viewportHeight) {
        setVerticalMode(false);
      } else {
        setVerticalMode(true);
      }
    };
    window.addEventListener('resize', handleResize);

    // 초기 사이즈 설정
    handleResize();

    setIsReady(true);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const theme = {
    "darkTheme": darkMode ? dark : light,
    "verticalMode": verticalMode
  }

  if (isReady) {
    return (
      <>
      <PreLoadingImage />
      <GlobalStyle/>
      <ThemeProvider theme={theme}>
        <IsDarkContext.Provider value={darkMode}>
          <Router basename={process.env.PUBLIC_URL}>
            <Routes>
              <Route
                path="/"
                element={
                  verticalMode
                  ? <Wrap_Mobile darkModeToggle={darkModeToggle} />
                  : <Wrap darkModeToggle={darkModeToggle} />
                }
              />
            </Routes>
          </Router>
        </IsDarkContext.Provider>
      </ThemeProvider>
      </>
    );
  }
}

export default App;
