import React, { forwardRef } from "react";
import styled from "styled-components";

const Main = styled.div`
    width: 100%;
    height: calc(98vh - 30vw);
    background-color: ${({theme}) => theme.darkTheme.bg4};
    padding: 7vw 5vw;
    border-radius: 0;
    overflow-y: scroll;
    position: relative;

    &::-webkit-scrollbar {
        width: 0;
        display: none;
    }
`;



function Container({children}, ref) {
    return(
        <Main ref={ref}>
            {children}
        </Main>
    );
}

export default forwardRef(Container);