import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import Profile from "./Profile";
import Top from "./Top";
import Bottom from "./Bottom";
import AboutContain from "./contain/AboutContain";
import Container from "./Container";
import CareerContain from "./contain/CareerContain";
import ProjectContain from "./contain/ProjectContain";

const Outside = styled.div`
    width: 100vw;
    height: 100vh;
    background-color: lightgray;
`;
const Main = styled.div`
    font-size: 4vw !important;
    width: 100vw;
    height: calc(99vh - 15vw);
    padding: 15vw 1vh 0;
    overflow: hidden;
    /* overflow-y: scroll;
    
    &::-webkit-scrollbar {
        width: 0;
        display: none;
    } */
    
    & *{
        border-width: 0.1px;
    }
    
    & > div{
        border-radius: 1.5vh;
    }
    
    & > *+*{
        margin-top: 1vh;
    }
`;

function Wrap(props) {
    const mainRef = useRef();
    const [menu, setMenu] = useState(0);

    const scrollToMenu = [
        [0, 0], // Profile
        //[0.5, 0.011], // AboutContain
        [1.03, 0.021], // CareerContain
        [0.73, 1.011], // 
        [0.43, 2.001], // 
    ];

    useEffect(()=>{
        const mainElement = mainRef.current;
        const vW = window.innerWidth;
        const vH = window.innerHeight;
        if (mainElement) {
            mainElement.scrollTo({
                top: scrollToMenu[menu][0] * vW + scrollToMenu[menu][1] * vH,
                behavior: 'smooth'
            });
        }
    },[menu]);

    const containerRefs = [
        useRef(), useRef()
    ];
    const containerScrollTop = () => {
        containerRefs.map((containerRef)=>{
            const containerElement = containerRef.current;
            if (containerElement) {
                containerElement.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
            }
        });
    }
    const projectScrollTop = () => {
        const projectElement = containerRefs[1].current;
        if (projectElement) {
            projectElement.scrollTop = 0;
        }
    }

    const [projectReset, setProjectReset] = useState(true);
    const containerReset = () => {
        containerScrollTop();
        setProjectReset(true);
    };

    return (
        <Outside>
            <Main ref={mainRef}>
                {/* <DarkChangeButton onClick={props.darkModeToggle}/> */}
                <Top />
                <Profile />
                <AboutContain/>
                <Container ref={containerRefs[0]}>
                    <CareerContain />
                </Container>
                <Container ref={containerRefs[1]}>
                    <ProjectContain
                        projectReset={projectReset}
                        setProjectReset={setProjectReset}
                        projectScrollTop={projectScrollTop}
                    />
                </Container>
                <Bottom
                    setMenu={setMenu}
                    containerReset={containerReset}
                />
            </Main>
        </Outside>
    );
}

export default Wrap;