import React, {useContext} from "react";
import styled, { css } from "styled-components";
import { IsDarkContext } from "../context/IsDarkContext";

const Main = styled.div`
    background-color: ${({theme}) => theme.darkTheme.bg4};
    width: 100%;
    height: 50vw;
    padding: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    & > div{
        display: flex;
    }

    & .name{
        font-size: 1.6em;
        color: ${({theme}) => theme.darkTheme.color4};
    }

    & span{
        font-size: 0.8em;
        & .today{
            font-size: 1em;
            color: ${({theme}) => theme.darkTheme.color5};
        }
    }
`;

const ProfileImage = styled.div`
    border-radius: 30% !important;
    width: 30vw !important;
    height: 30vw !important;
    flex-shrink: 0;
    overflow: hidden;
    margin-right: 5vw;
    
    & > img{
        width: 100%;
        height: 100%;
    }
`;

const Intro = styled.div`
    width: 100%;
    height: 15%;
    padding-left: 5vw;

    & img{
        height: 100%;
        flex-shrink: 0;
        margin-right: 2vw;
        border-radius: 30% !important;
    }
`;


function Profile(props) {
    const isDark = useContext(IsDarkContext);

    return(
        <Main>
            <div>
                <ProfileImage>
                    <img src={`${process.env.PUBLIC_URL}/images/profile.png`} alt="profile" />
                </ProfileImage>
                <div>
                    <div>
                        Today <span><span className="today">155</span> | 16516</span>
                    </div>
                    <div className="name">
                        김소중
                    </div>
                    <div>
                        안되면 될 때 까지! 집요한 개발자 김소중입니다
                    </div>
                </div>
            </div>
            <Intro>
                <img src={isDark?`${process.env.PUBLIC_URL}/images/git2.png`:`${process.env.PUBLIC_URL}/images/git.png`} alt="birth" />
                <img src={isDark?`${process.env.PUBLIC_URL}/images/velog2.png`:`${process.env.PUBLIC_URL}/images/velog.png`} alt="home" />
            </Intro>
        </Main>
    );
}

export default Profile;